<template>

  <div class="catalog__list">
      <ProductFilter :price-from.sync="filterPriceFrom"
                     :price-to.sync="filterPriceTo"
                     :category-id.sync="filterCategoryId"
                     :colors-filter.sync="filterColor"
                     :colors="colors"
      />

    <div v-if="productsLoading"> <LoadingPart class="loading-position"/></div>
    <div v-if="productsLoadingFailed">Error loading page <button @click.prevent="loadProducts">Try again</button></div>

      <section class="content__catalog">
        <ProductItem
            v-for="product in products"
            :key="product.id"
            :product="product"
        />
      </section>

      <BasePagination
          v-model="page"
          :count="countProducts"
          :per-page="productsPerPage"
      />
  </div>
</template>

<script>
  import ProductItem from "./ProductItem";
  import BasePagination from "./BasePagination";
  import ProductFilter from "./ProductFilter";
  import {API_BASE_URL} from "../config";
  import axios from 'axios';
  import LoadingPart from "./LoadingPart";

  export default {
    components: {
      LoadingPart,
      ProductItem,
      BasePagination,
      ProductFilter
    },
    data(){
      return {
        filterPriceFrom: 0,
        filterPriceTo: 0,
        filterCategoryId: 0,
        filterColor: 0,

        page: 1,
        productsPerPage: 4,

        productsData: null,
        colorsData: null,

        productsLoading: false,
        productsLoadingFailed: false,
      }
    },
    computed: {
      products(){
        return this.productsData ?
            this.productsData.items.map(product => {
              return {
                ...product,
                image: product.image.file.url
              }
            })
            : [];
      },
      countProducts() {
        return this.productsData ? this.productsData.pagination.total : 0;
      },

      colors(){
        return this.colorsData ? this.colorsData.items : [];
      }
    },
    methods: {
      loadProducts(){
        this.productsLoading = true;
        this.productsLoadingFailed = false;
        clearTimeout(this.loadProductsTimer);
          this.loadProductsTimer = setTimeout(()=> {
            axios.
              get(API_BASE_URL + `/api/products`,{
                params: {
                  page: this.page,
                  limit: this.productsPerPage,
                  categoryId: this.filterCategoryId,
                  minPrice: this.filterPriceFrom,
                  maxPrice: this.filterPriceTo,
                  colorId: this.filterColor
                }
              })
                .then(response => this.productsData = response.data)
                .catch(()=> this.productsLoadingFailed = true)
                .then(()=> this.productsLoading = false);
          }, 1500);
      },
      loadColors(){
        axios.get(API_BASE_URL + '/api/colors')
          .then(response => this.colorsData = response.data)
      }
    },
    watch: {
      page(){
        this.loadProducts()
      },
      filterPriceFrom(){
        this.loadProducts();
      },
      filterPriceTo(){
        this.loadProducts();
      },
      filterCategoryId(){
        this.loadProducts();
      },
      filterColor(){
        this.loadProducts();
      }
    },
    created() {
      this.loadProducts();
      this.loadColors()
    }
  }
</script>

<style>
  .loading-position {
    position: relative;
    top: 300px;
    left: 320px;
  }
</style>